import { graphql, useStaticQuery } from "gatsby"
import React from "react"
import CarouselSlide from "../components/carousel"


const GolvLaggningCarousel = () => {
  const { gcms: { golvlaggningBilders } } = useStaticQuery(graphql`
    {
        gcms {
            golvlaggningBilders {
                bilder {
                  url
                }
                desc
                title
              }
    }
}
  `)


  return (

    <CarouselSlide images={golvlaggningBilders.map(x => {return {url:x.bilder.url, desc:x.desc, title:x.title}})} />

  )
}
export default GolvLaggningCarousel;